import React, { useEffect } from "react";
import { Button, Card } from "react-bootstrap";
import AuthorInfo from "../../Analytics/content/components/authorInfo";
import { useDispatch } from "react-redux";
import { baseUrl, isAppleSafari, showAddAccountAlertNew, } from "../../../../../utils";
import { Link } from "react-router-dom";
import parse from "html-react-parser";
import SocialactionSkeleton from "./skeleton/socialactionSkeleton";
import { MediaBaseUrl } from "../../../../../actions/api";
import { addUserAccount } from "../../../../Authorization/SocialAuth/connectSocialAcc";
import { social_account } from "../../../../../actions/dashboardActions";
import { FontAwesomeIcon } from "../../../../CommonComponents/AssetsHelper";

const SocialAction = (props) => {
    const { connectedAccounts, feeds, loader } = props;
    const channel = isAppleSafari() ? window : new BroadcastChannel("connect");
    const dispatch = useDispatch();

    const connectaccount = (item, reconnect) => {
        let refresh = 0;
        if (reconnect) { refresh = 1; }
        localStorage.setItem("refresh", refresh);
        dispatch(showAddAccountAlertNew(item, "dashboardConnect"));
    };

    const readAuthenticationCode = (event) => {
        const response = JSON.parse(event.data);
        const { data, networkId, type } = response;
        if (type == "dashboardConnect")
            addUserAccount(networkId, data, () => dispatch(social_account()), localStorage.getItem("ownerId"), 0, Number(localStorage.getItem("refresh")));
        localStorage.removeItem("refresh");
    };

    useEffect(() => {
        channel.addEventListener("message", readAuthenticationCode);
        return () => { channel.removeEventListener("message", readAuthenticationCode); };
    }, []);

    return (
        <div className="col-md-6 mb-3">
            <div className="socialAccount-post-section h-100">
                <Card className="p-0 card-custom card-stretch">
                    <Card.Header className="p-3">
                        <div className="m-0 d-flex align-items-center justify-content-between card-title h5 w-100">
                            <h6 className="card-label font-weight-bolder m-0 "> Social Accounts </h6>
                            {connectedAccounts && Object.keys(connectedAccounts).length ? (
                                <div className=""> <Link to={`/transactions#connectedAccounts`} className="text-secondary font-size-base" > View All </Link> </div>
                            ) : null}
                        </div>
                    </Card.Header>
                    <Card.Body className="p-3">
                        {loader ? (<SocialactionSkeleton />)
                            : (feeds && Object.keys(feeds).length) || (connectedAccounts && Object.keys(connectedAccounts).length) ? (
                                <>
                                    {feeds.map((feeddata, index) => {
                                        var btntext = "";
                                        if (feeddata.Feed.accountId == "") { btntext = "Connect"; } 
                                        else { btntext = "Reconnect"; }

                                        return (
                                            <div className="mb-3">
                                                <div className="mb-4 last-child-mb-0 d-block text-secondary">
                                                    <div className="p-3 border rounded">
                                                        <div className="d-flex align-items-center pt-2 pb-2 users_row justify-content-between" key={index} >
                                                            <div className="d-flex align-items-center flex-grow-1">
                                                                <div className="symbol symbol-45 symbol-light me-4 con_user_img ms-1 flex-shrink-0">
                                                                    {!["fa-twitter"].includes(feeddata.Network.icon) ?
                                                                        <span style={{ backgroundImage: `url(${baseUrl(`social-icons-feed/${feeddata.Network.icon}.svg`)})`, backgroundSize: "70%", backgroundPosition: "center", backgroundRepeat: "no-repeat", }} ></span>
                                                                        : <FontAwesomeIcon type={'brands'} icon={'square-x-twitter'} iconClass={''} />
                                                                    }
                                                                </div>

                                                                <div className="d-flex text-left flex-column conn_user_info w-75 flex-grow-1">
                                                                    <p title="From Hashtag(#)" className="text-dark m-0 text-nowrap d-flex align-items-center" >
                                                                        <strong className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis mx-width-250 text-capitalize"> {parse(feeddata.Feed.feedName)} </strong>
                                                                        {feeddata.Feed.socialLink ? (<Link to={feeddata.Feed.socialLink} target="_blank" > <i className="fa fa-external-link-alt icon-nm ms-1"></i> </Link>) : null}
                                                                    </p>
                                                                </div>
                                                            </div>

                                                            <div className="conn_status disconnnected text-right">
                                                                <div className="conn_status_wrap">
                                                                    {![10, 19, 15, 7, 23, 29].includes(feeddata.Network.id) && (
                                                                        <a onClick={() => connectaccount(feeddata.Network, true)} className="btn btn-outline-primary openGoogleReconnect btn-xs" data-action="reconnect" data-src={``}> <span> {btntext}</span> </a>
                                                                    )}
                                                                </div>
                                                                <small className="font-size-sm font-size-sm-network">{feeddata.Network.name} Profile</small>
                                                            </div>

                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        );
                                    })}

                                    {connectedAccounts && Object.keys(connectedAccounts).length
                                        ? connectedAccounts.map((e) => {
                                            const getData = e.ConnectedAccounts;
                                            return (
                                                <div className="mb-3">
                                                    <div className="mb-4 last-child-mb-0 d-block text-secondary">
                                                        <div className="p-3 border rounded">
                                                            {" "}
                                                            <div className=" d-flex align-items-center justify-content-between">
                                                                <AuthorInfo isIcon={e.Network.icon} picture={getData.profilePicture} authorUsername={e.accountName} authorName={getData.accountName} link={""} networkId={getData.networkId} />
                                                                <div className="">
                                                                    <h5>
                                                                        {getData.api == 2 ? (
                                                                            <div className="conn_status connnected text-right">
                                                                                <div className="conn_status_wrap font-size-lg m-0">
                                                                                    <p className="font-weight-bolder acc_status m-0">
                                                                                        <FontAwesomeIcon type={'regular'} icon={'check'} iconClass={'me-1 text-success'} />{" "}
                                                                                        Connected
                                                                                    </p>
                                                                                </div>
                                                                                <small className="font-size-sm font-size-sm-network"> {e.Network.name} Profile </small>
                                                                            </div>
                                                                        ) : (
                                                                            <div className="conn_status disconnnected text-right">
                                                                                <div className="conn_status_wrap">
                                                                                    {![10, 19, 15, 7, 23, 29].includes( e.Network.id ) && (
                                                                                            <Button variant="outline-primary" onClick={() => connectaccount(e.Network, true)} className="openGoogleReconnect btn-xs" data-action="reconnect" data-src={``}> <span>Reconnect</span> </Button>
                                                                                        )}
                                                                                </div>
                                                                                <small className="font-size-sm font-size-sm-network">{e.Network.name} Profile</small>
                                                                            </div>
                                                                        )}
                                                                    </h5>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            );
                                        })
                                        : null}
                                </>
                            ) : (
                                <div className="not_connected_err not_connected_err_new flex-center overlay-layer d-flex flex-column flex-center" style={{ backgroundImage: `url(${MediaBaseUrl}error/not-connected-new.png)`, }} >
                                    <div className="not_conne_overlay d-flex flex-column flex-center"> <Link to={`/transactions#connectedAccounts`} className="btn btn-primary" > Connect </Link> </div>
                                </div>
                            )}
                    </Card.Body>
                </Card>
            </div>
        </div>
    );
};

export default React.memo(SocialAction);
